import { Account } from "../../../hooks/use_account";
import { SmartState, useSmartState } from "../../../hooks/use_smart_state";
import { securePage } from "../../../components/common/secure_page";
import Form from "../../../components/common/form/form";
import Title from "../../../components/common/form/title";
import { formParentManageAccount } from "../manage-account";
import Box from "../../../components/common/form/box";
import P from "../../../components/common/form/p";
import Input from "../../../components/common/form/input";
import SubmitButton from "../../../components/common/form/submit-button";
import { formRequestAccountManagerAccountMobile } from "../../../lib/shared/form-request-makeup/requests/account/manage-account/mobile";
import Desktop from "../../../components/common/desktop";
import Mobile from "../../../components/common/mobile";

export type AccountManageAccountMobilePageProps = {
    result: {};
    account: SmartState<Account | null>;
};

const AccountManageAccountMobilePage = securePage(function({result,account}: AccountManageAccountMobilePageProps) {
    const { user: { phone } } = account.value;

    return (<>
        <div className="account-manage-account-mobile-page">            
            <Form 
                requestMakeup={formRequestAccountManagerAccountMobile}
                parent={formParentManageAccount}
                autoFocus
                input={{
                    maxWidth: 300
                }}
                onSuccess={() => {
                    return {
                        type: "redirect",
                        path: "/account/manage-account",
                        message: {
                            type: "info",
                            message: "The mobile phone associated with your account has been updated."
                        }
                    };
                }}
            >
                <Desktop>
                    <Title>Change your mobile number</Title>
                </Desktop>
                <Mobile>
                    <Title>Change mobile number</Title>
                </Mobile>                
                <Box border>
                    <P>
                        If you want to change the mobile phone associated with your TBD Liquids account, you can do so below.
                    </P>
                    <Input name="phone" initialValue={phone} type="mobile" />
                    <SubmitButton caption="Save" disableIfUnchanged={["phone"]} />
                </Box>
            </Form>
        </div>
        <style jsx>{`
            .account-manage-account-mobile-page {
                /* min-height: 600rem; */
                width: 100%;
            }
        `}</style>
    </>
);
});

export default AccountManageAccountMobilePage;