export type FormError<TValues extends FormValues = FormValues> = {
    message: string;
    inputNames: (keyof TValues)[];
};

export type FormInputValidation<TValues extends FormValues = FormValues> = string | ((value: string) => string | FormError<TValues> | void);

export type FormValues = {[name: string]: string};

export type FormValidation<TValues extends FormValues = FormValues> = (values: TValues) => string | FormError<TValues> | void;

export type FormRequestMakeupInput<TValues extends FormValues = FormValues> = {
    caption: string;
    required?: boolean;
    placeholder?: string;
    defaultInitialValue?: string;
    emptyValue?: string | RegExp;
    validation?: FormInputValidation<TValues>;
};

export type FormRequestMakeupInputs<TValues extends FormValues = FormValues> = {
    [name: string]: FormRequestMakeupInput<TValues>;
};

export type AlternativeSubmit = {
    relativeUrl: string;
    emptyForm?: boolean;
};

export type FormRequestMakeup<TValues extends FormValues = FormValues> = {
    trimValues?: true | string[];
    validation?: FormValidation<TValues>;
    preValidationAlterations?: (values: TValues,inputs: FormRequestMakeupInputs<TValues>) => void;
    inputs: FormRequestMakeupInputs<TValues>;
    relativeUrl: string;
    errorTable?: {
        [id: string]: string;
    };
    alternativeSubmits?: {
        [name: string]: AlternativeSubmit;
    };
};

export const commonEmptyValues = {
    phone: /^(?:\d*\/)?\s*$/
};